import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import Section from '../section';
import SectionTitle from '../section-title';
import InstagramIcon from '../../images/icon/trainer_instagram.svg';
// import TwitterIcon from '../../images/icon/trainer_twitter.svg';
// import TiktokIcon from '../../images/icon/trainer_tiktok.svg';

const SectionTrainer = () => {
  const { allImageSharp } = useStaticQuery(graphql`
    query {
      allImageSharp {
        nodes {
          id
          gatsbyImageData
          fluid {
            originalName
          }
        }
      }
    }
  `);

  return (
    <Section id='trainer' theme='light'>
      <SectionTitle theme='light'>Trainer</SectionTitle>
      <Grid container spacing={4}>
        {[
          {
            originalName: '640x640-trainer-calvin.jpg',
            name: 'ジョー',
            en_name: 'Joe',
            description:
              '自重トレーニングで理想の体づくりを得意としております。カナダと日本のハーフで日本語・英語・フランス語対応可能です。栄養学も学んでおり、食事面のサポートも可能です。健康的で効率的なトレーニングをしましょう！',
            sns: [
              {
                image: InstagramIcon,
                link: 'https://www.instagram.com/workout828official/'
              }
            ]
          },
          {
            originalName: '640x640-trainer-kashima.jpg',
            name: '鹿島 義慈',
            en_name: 'Yoshinari Kashima',
            description:
              '鍼灸指圧師の資格を持っており、身体に関する知識から、正しいフォーム指導や、効率よく筋肉を付けるトレーニングメソッド・食事、ダイエットまで幅広く対応可能です。これまで男女年齢問わず、多くの指導実績有り。楽しく続けて理想の身体を手に入れましょう。',
            sns: []
          }
        ].map(({ originalName, name, en_name, description, sns }, i) => {
          const { gatsbyImageData } = allImageSharp.nodes.find(
            ({ fluid }) => fluid.originalName === originalName
          );

          return (
            <Grid item key={i} md={6}>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <GatsbyImage
                    image={gatsbyImageData}
                    alt={en_name}
                    layout='fullWidth'
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Typography
                    sx={{
                      fontSize: '24px',
                      lineHeight: '26px'
                    }}
                  >
                    {name}
                  </Typography>
                  <Typography
                    sx={{
                      fontSize: '12px',
                      lineHeight: '26px',
                      letterSpacing: '0.6px',
                      color: '#b28223'
                    }}
                  >
                    {en_name}
                  </Typography>
                  <Typography
                    sx={{
                      marginTop: '1em',
                      lineHeight: '26px'
                    }}
                  >
                    {description}
                  </Typography>
                  <Box
                    sx={{
                      mt: 1
                    }}
                  >
                    {sns.map(({ image, link }, i) => (
                      <Link
                        key={i}
                        href={link}
                        target='_blank'
                        sx={{
                          mr: 1
                        }}
                      >
                        <img src={image} alt={name} width={36} height={36} />
                      </Link>
                    ))}
                  </Box>
                </Grid>
              </Grid>
            </Grid>
          );
        })}
      </Grid>
    </Section>
  );
};

export default SectionTrainer;
